
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Toast from 'vux/src/components/toast/index.vue'

import Footer from '../components/Footer.vue'
import { mapState } from 'vuex'

export default {
name: "Account",
directives: {
TransferDom
},
components: {
Footer,
Cell,
Group,
Confirm,
AlertModule,
Popup,
XButton,
XInput,
Toast,
},
data () {
return {
loginDialog: false,
userid: "",
userpassword: "",
showpassword: false,
showid: false,
userName: '点击登录',
}
},
created() {
this.userName =  sessionStorage.getItem("userid")  || '点击登录';
},
methods: {

showModule () {
var that = this;
AlertModule.show({
title: '提示',
content: '请先登录！',
maskZIndex: 5000,
dialogTransition: "none",
onHide: () =>  {
that.loginDialog = !that.loginDialog;
}
});
},

openLoginDialog () {
if ( this.userName === '点击登录') {
this.loginDialog = !this.loginDialog;
}
},
login () {
if (this.userid != 'admin') {
this.showid = !this.showid;
}
else if ( this.userpassword != 123456 ){
this.showpassword = !this.showpassword;
}
else {
this.loginDialog = !this.loginDialog;
sessionStorage.setItem("userid", "admin");
this.userName = sessionStorage.getItem("userid")  || '点击登录';
}
}
},
}



